.app {
  background-color: #222222;
}

.nav-bar {
  height: 40px;
  background-color: #444444;
  color: #888888;
  -webkit-box-shadow: 0px 10px #333333;
          box-shadow: 0px 10px #333333;
}

.nav-block {
  text-decoration: none;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
  height: 100%;
  min-width: 100px;
  line-height: 250%;
  display: inline-block;
  border: 0px;
  outline: 0px;
  color: #ffab78;
  background-color: #c56636;
  -webkit-box-shadow: 0px 10px #823e25;
          box-shadow: 0px 10px #823e25;
  margin-right: 10px;
  float: left;
}

.nav-button {
  -webkit-transition: 0.15s;
  -o-transition: 0.15s;
  transition: 0.15s;
  color: #333333;
  background-color: #5f5f5f;
  -webkit-box-shadow: 0px 10px #3f3f3f;
          box-shadow: 0px 10px #3f3f3f;
  cursor: pointer;
}
.nav-button-active {
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
  cursor: default;
  color: #ffab78;
  background-color: #c56636;
  -webkit-transform: translateY(4px);
      -ms-transform: translateY(4px);
          transform: translateY(4px);
  -webkit-box-shadow: 0px 6px #823e25;
          box-shadow: 0px 6px #823e25;
}

.page-container {
  position: static;
  display: block;
}

.page-content {
  color: #666666;
  height: 100vh;
  margin-top: 20px;
}

.page-body {
  margin-left: 20px;
  margin-right: 20px;
}

.left {
  float: left;
}

.right {
  float: right;
}



